i.loading-fa-icon {
    display: none;
}

@mixin loading-fa-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;

    $size: 36px;

    font-size: $size;
    width: $size;
    height: $size;
    margin-top: (-$size / 2);
    margin-left: (-$size / 2);
    color: #b02a1a;
}

@mixin loading-fa-icon-content {
    position: relative;

    i.loading-fa-icon {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;

        $size: 36px;

        font-size: $size;
        width: $size;
        height: $size;
        text-align: center;
        line-height: $size;
        margin-top: (-$size / 2);
        margin-left: (-$size / 2);
        color: #b02a1a;
    }
}

@mixin loading-fa-icon-content-bg {
    @include loading-fa-icon-content;

    &:after {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.5);
        content: ' ';
        display: block;
    }
}
