//
// VARIABLES
//

/// Main Color $variables
$white: #ffffff !default;
$black: #000000 !default;
$gray: #333333 !default;
$gray-light: #999999 !default;
$gray-lighter: #eeeeee !default;
$gray-dark: #333333 !default;
$gray-darker: #222222 !default;
$grey1: #f7f7f7 !default;
$grey2: #e7e7e7 !default;
$grey3: #cccccc !default;
$grey4: #999999 !default;
$grey5: #666666 !default;
$grey6: #333333 !default;
$blue2: #333366 !default;
$blue1: #004386 !default;
$blue: #00739b !default;
$green: #669933 !default;
$purple: #8f2b8c !default;
$sandyGrey: #eae9e7 !default;
$sandyGreyDark: #e0dbd7 !default;
$sandyGray: $sandyGrey !default;
$sandyGrayDark: $sandyGreyDark !default;

/// City Brand Color $variables
$red: #b02a1a;
$red1: $red; // links, lightest red used for text
$red2: #96190b; // darker red
$red3: #a0271f; // darker still
$red4: #670003; // visited links
$logo-red: #e31b25; // lightest red, hover colour on links
$city-blue: #00759b; // from brand toolkit
$city-blue-light: lighten($city-blue, 10%); //lighter version of above
$city-orange: #e55302; // from brand toolkit
$city-green: #6c953c; // from brand toolkit
$city-purple: #3a0133; // from city.ac.uk/library
$city-yellow: #fab92d; // for city.ac.uk/cityventures
$city-teal: #115c66; // for city.ac.uk/cityventures

/// External Brand Color $variables
$facebook-blue: #3b5998;
$twitter-blue: #00aced;
$googleplus-orange: #dd4b39;
$linkedin-blue: #0976b4;

/// Accent Color $variables
$orange: #e55302 !default;
$blue-dark: #0064cd !default;
$yellow: #ffc40d !default;
$pink: #c3325f !default;

/// Base Page Color $variables
$body-bg: $white !default;
$text-color: $gray-dark !default;

/// Link Color $variables
$link-color: #b02a1a !default;
$link-hover-color: #ef383f !default;
$link-hover-decoration: underline !default;
$link-colorDark: #96190b !default;
$link-colorDarker: #a0271f !default;
$link-colorVisited: #670003 !default;

////
/// Anchor Variables
////
/// @type Color
$anchor-color: #b91b1b;
/// @type Color
$anchor-color--dark: #670003;
/// @type Color
$anchor-color-alt: #555555;
/// @type Color
$anchor-color-alt--light: #999999;
/// @type Color
$anchor-color-alt--lighter: #cccccc;
/// @type Color
$anchor-color-alt--lightest: #eeeeee;
/// @type Length
$anchor-border-width: 1px;
/// @type Length
$anchor-border-width--heading: 2px;

/// Menu Color $variables
$activeGreen: #938210 !default;
$dropdownGrey: #a7a7a7 !default;
$hoverGrey: #909090 !default;

/// Font Family $variables
$font-family-sans: Arial, Helvetica, sans-serif !default;
$font-family-meta-sans: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
    sans-serif !default;
$font-family-serif: Georgia, 'Times New Roman', Times, serif !default;
$font-family-monospace: 'Courier New', Courier, monospace !default;
$font-family-base: $font-family-sans !default;
$font-family-alt: $font-family-serif !default;

/// Font Size $variables
$font-size-base: 14px !default;
$font-size-base-smaller: $font-size-base - 2;
$line-height-base: 1.5 !default; // 21px/14px
// Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed: floor(
    ($font-size-base * $line-height-base)
) !default; // ~20px

/// Headings Font Family $variables
$headings-font-family: $font-family-serif !default; // empty to use BS default, $font-family-base
$headings-font-weight: bold !default; // instead of browser default, bold
$headings-line-height: $line-height-base;
$headings-color: inherit !default; // empty to use BS default, $text-color

/// Headings Font Size $variables
$font-size-h1: floor(($font-size-base * 2.6)) !default; // ~36px
$font-size-h2: floor(($font-size-base * 2.15)) !default; // ~30px
$font-size-h3: ceil(($font-size-base * 1.7)) !default; // ~24px
$font-size-h4: ceil(($font-size-base * 1.25)) !default; // ~18px
$font-size-h5: $font-size-base !default;
$font-size-h6: ceil(($font-size-base * 0.85)) !default; // ~12px

/// Alternate Text $variables
$text-muted: $gray-light !default; // Text muted color
$abbr-border-color: $gray-light !default; // Abbreviations and acronyms border color
$headings-small-color: $gray-light !default; // Headings small color
$blockquote-small-color: $gray-light !default; // Blockquote small color
$blockquote-font-size: (
    $font-size-base * 1.25
) !default; // Blockquote font size
$blockquote-border-color: $gray-lighter !default; // Blockquote border color
$page-header-border-color: $gray-lighter !default; // Page header border color
$placeholderText: $gray-light !default;

/// Code Color $variables
$code-color: #c7254e !default;
$code-bg: #f9f2f4 !default;
$kbd-color: #ffffff !default;
$kbd-bg: #333333 !default;
$pre-bg: #f5f5f5 !default;
$pre-color: $gray-dark !default;
$pre-border-color: #cccccc !default;

$pre-scrollable-max-height: 340px !default;

/// Component sizing $variables
/// based on 14px font-size and 20px line-height
$font-size-large: $font-size-base * 1.25; // ~18px
$font-size-small: $font-size-base * 0.85; // ~12px
$font-size-mini: $font-size-base * 0.75; // ~11px
$padding-large: 11px 19px; // 44px
$padding-small: 2px 10px; // 26px
$padding-mini: 1px 6px; // 24px
$border-radius-base: 4px;
$border-radius-large: 6px;
$border-radius-small: 3px;

/// Grid $variables
$grid-columns: 24 !default;
$grid-column-width: 30px !default;
$grid-gutter-width: 20px !default;
$grid-row-width: ($grid-columns * $grid-column-width) +
    ($grid-gutter-width * ($grid-columns - 1)) !default;

/// Fluid Grid $variables
$fluidGridColumnWidth: percentage($grid-column-width/$grid-row-width) !default;
$fluidGridGutterWidth: percentage($grid-gutter-width/$grid-row-width) !default;
$responsive-padding: 5px; //padding for responsive design

/// Media Queries breakpoints $variables
// Extra small screen / phone
// Note: Deprecated $screen-xs and $screen-phone as of v3.0.1
$screen-xs: 480px !default;
$screen-xs-min: $screen-xs !default;
$screen-phone: $screen-xs-min !default;
// Small screen / tablet
// Note: Deprecated $screen-sm and $screen-tablet as of v3.0.1
$screen-sm: 768px !default;
$screen-sm-min: $screen-sm !default;
$screen-tablet: $screen-sm-min !default;
// Medium screen / desktop
// Note: Deprecated $screen-md and $screen-desktop as of v3.0.1
$screen-md: 992px !default;
$screen-md-min: $screen-md !default;
$screen-desktop: $screen-md-min !default;
// Large screen / wide desktop
// Note: Deprecated $screen-lg and $screen-lg-desktop as of v3.0.1
$screen-lg: 1200px !default;
$screen-lg-min: $screen-lg !default;
$screen-lg-desktop: $screen-lg-min !default;
// So media queries don't overlap when required, provide a maximum
$screen-xs-max: ($screen-sm-min - 1) !default;
$screen-sm-max: ($screen-md-min - 1) !default;
$screen-md-max: ($screen-lg-min - 1) !default;

/// Container size $variables
// Define the maximum width of `.container` for different screen sizes.
// Small screen / tablet
$container-tablet: ((720px + $grid-gutter-width)) !default;
// For `$screen-sm-min` and up.
$container-sm: $container-tablet !default;
// Medium screen / desktop
$container-desktop: ((940px + $grid-gutter-width)) !default;
// For `$screen-md-min` and up.
$container-md: $container-desktop !default;
// Large screen / wide desktop
$container-large-desktop: ((1140px + $grid-gutter-width)) !default;
// For `$screen-lg-min` and up.
$container-lg: $container-large-desktop !default;

/// Table $variables
$tableBackground: transparent !default; // overall background-color
$tableBackgroundAccent: #f9f9f9 !default; // for striping
$tableBackgroundHover: #f5f5f5 !default; // for hover
$tableBorder: #dddddd !default; // table and cell border

/// Button $variables
$btnBackground: $white !default;
$btnBackgroundHighlight: darken($white, 10%) !default;
$btnBorder: #cccccc !default;

$btnPrimaryBackground: $link-color !default;
$btnPrimaryBackgroundHighlight: adjust-hue($btnPrimaryBackground, 15%) !default;

$btnInfoBackground: #5bc0de !default;
$btnInfoBackgroundHighlight: #2f96b4 !default;

$btnSuccessBackground: #62c462 !default;
$btnSuccessBackgroundHighlight: #51a351 !default;

$btnWarningBackground: lighten($orange, 15%) !default;
$btnWarningBackgroundHighlight: $orange !default;

$btnDangerBackground: #ee5f5b !default;
$btnDangerBackgroundHighlight: #bd362f !default;

$btnInverseBackground: $gray !default;
$btnInverseBackgroundHighlight: $gray-darker !default;

/// Form $variables
$inputBackground: $white !default;
$inputBorder: #cccccc !default;
$inputBorderRadius: $border-radius-base !default;
$inputDisabledBackground: $gray-lighter !default;
$formActionsBackground: #f5f5f5 !default;
$inputHeight: $line-height-base + 10px; // base line-height + 8px vertical padding + 2px top/bottom border

/// Dropdown $variables
$dropdownBackground: $white !default;
$dropdownBorder: rgba(0, 0, 0, 0.2) !default;
$dropdownDividerTop: #e5e5e5 !default;
$dropdownDividerBottom: $white !default;
$dropdownLinkColor: $gray-dark !default;
$dropdownLinkColorHover: $white !default;
$dropdownLinkColorActive: $dropdownLinkColor !default;
$dropdownLinkBackgroundActive: $link-color !default;
$dropdownLinkBackgroundHover: $dropdownLinkBackgroundActive !default;

/// Z-index $variables
$zindexDropdown: 1000 !default;
$zindexPopover: 1010 !default;
$zindexTooltip: 1020 !default;
$zindexFixedNavbar: 1030 !default;
$zindexModalBackdrop: 1040 !default;
$zindexModal: 1050 !default;

/// Sprite icons path $variables
// $iconSpritePath:      "../img/glyphicons-halflings.png" !default;
// $iconWhiteSpritePath: "../img/glyphicons-halflings-white.png" !default;

// Horizontal Rule $variables
$hr-border: $gray-lighter !default;

/// Horizontal forms & lists $variables
$component-offset-horizontal: 180px;

// Horizontal description list $variables
$dl-horizontal-offset: $component-offset-horizontal !default;

/// Navbar Container $variables
$navbarHeight: 40px !default;
$navbarBackground: $gray-darker !default;
$navbarBackgroundHighlight: $gray-dark !default;

/// Navbar Link $variables
$navbarText: $gray-light !default;
$navbarLinkColor: $gray-light !default;
$navbarLinkColorHover: $white !default;
$navbarLinkColorActive: $navbarLinkColorHover !default;
$navbarLinkBackgroundHover: transparent !default;
$navbarLinkBackgroundActive: $navbarBackground !default;

/// Navbar Search $variables
$navbarSearchBackground: lighten($navbarBackground, 25%) !default;
$navbarSearchBackgroundFocus: $white !default;
$navbarSearchBorder: darken($navbarSearchBackground, 30%) !default;
$navbarSearchPlaceholderColor: #cccccc !default;
$navbarBrandColor: $navbarLinkColor !default;

/// Navbar Grid $variables
// Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint: $screen-sm-min !default;
// Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1) !default;

/// Hero unit $variables
$heroUnitBackground: $gray-lighter !default;
$heroUnitHeadingColor: inherit !default;
$heroUnitLeadColor: inherit !default;

/// Form states and alert $variables
$state-warning-text: #c09853 !default;
$state-warning-bg: #fcf8e3 !default;
$warningBorder: darken(adjust-hue($state-warning-bg, -10), 3%) !default;

$state-danger-text: #b94a48 !default;
$state-danger-bg: #f2dede !default;
$errorBorder: darken(adjust-hue($state-danger-bg, -10), 3%) !default;

$state-success-text: #468847 !default;
$state-success-bg: #dff0d8 !default;
$successBorder: darken(adjust-hue($state-success-bg, -10), 5%) !default;

$state-info-text: #3a87ad !default;
$state-info-bg: #d9edf7 !default;
$infoBorder: darken(adjust-hue($state-info-bg, -10), 7%) !default;

@import 'strategic-plan-variables';

/// Bootstrap 3 Brand $variables
$brand-primary: #428bca !default;
$brand-success: #5cb85c !default;
$brand-info: #5bc0de !default;
$brand-warning: #f0ad4e !default;
$brand-danger: #d9534f !default;

/// Bootstrap 3 Thumbnail $variables
$thumbnail-padding: 4px !default;
$thumbnail-bg: $body-bg !default;
$thumbnail-border: #dddddd !default;
$thumbnail-border-radius: $border-radius-base !default;
$thumbnail-caption-color: $text-color !default;
$thumbnail-caption-padding: 9px !default;

/// Extend Bootstrap 3.2.0 visible-x classes
.visible-phone {
    @extend .visible-xs-block;
}
.visible-tablet {
    @extend .visible-sm-block;
}
.visible-desktop {
    @extend .visible-md-block;
}
.visible-widescreen {
    @extend .visible-lg-block;
}
@media (max-width: $screen-xs-max) {
    @include responsive-invisibility('.hidden-phone');
}
@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
    @include responsive-invisibility('.hidden-tablet');
}
@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
    @include responsive-invisibility('.hidden-desktop');
}
@media (min-width: $screen-lg-min) {
    @include responsive-invisibility('.hidden-widescreen');
}
